<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth, createUrl } from '../utils/utils.js'
import DispatchTrail from './dispatchTrail.vue'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
        // 合作主体: '',
        目前所在地: 'scenicSpotName',
        机器人编号: 'robotCode',
        机器人型号: 'clientVersion',
        // 机器人型号: 'robotModel',
        机器人批次号: 'robotBatchNumber',
        出厂日期: 'dateProduction',
        使用年限: 'serviceLife',
        '主机出厂成本/元': 'factoryCost',
        // 外设成本: '',
        // 成本: 'cost',
        '累计折旧/元': 'accumulate',
        '净值/元': 'netPrice',
        健康状态: 'robotFaultState',
        设备状态: 'equipmentStatus',
		故障状态:'errorRecordsAffect',
        机器人归属公司: 'ascriptionCompanyName',
        '当日运营时长/小时': 'dayTime',
        '当月运营时长/小时': 'monthTime',
        '当年运营时长/小时': 'yearTime',
        '累积运营时长/小时': 'runTime',
        电池质保截止日期: 'batteryWarrantyDeadline',
        机器人质保截止日期: 'robotWarrantyDeadline',
        更新时间: 'updateDate'
      },
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      robotOptions: [], // 提交时的机器人选项
      companyOptions: [], // 提交时的公司选项

      searchEquipmentStatusOptions: [
        // {
        //   value: '10',
        //   label: '在产'
        // },
        {
          value: '20',
          label: '库存'
        },
        {
          value: '30',
          label: '运营'
        },
        {
          value: '40',
          label: '报废'
        }
      ], // 搜索表单设备状态方式选项(在产、库存、运营、报废)

      equipmentStatusOptions: [
        // {
        //   value: '10',
        //   label: '在产'
        // },
        {
          value: '20',
          label: '库存'
        },
        {
          value: '30',
          label: '运营'
        },
        {
          value: '40',
          label: '报废'
        }
      ], // 提交表单设备状态方式选项(在产、库存、运营、报废)

      // 当日运行时长、当月运行时长、当年运行时长、累计运行时长

      // dateStateOptions: [
      //   {
      //     value: '1',
      //     label: '按月查询'
      //   },
      //   {
      //     value: '2',
      //     label: '按日查询'
      //   },
      //   {
      //     value: '3',
      //     label: '按年查询'
      //   }
      // ], // 日期统计方式选项
      // typeOptions: [
      //   {
      //     value: '1',
      //     label: '按订单数量统计'
      //   },
      //   {
      //     value: '2',
      //     label: '按订单计费金额统计'
      //   }
      // ], // 景区选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数
      total: 0, // 总页数

