     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/addRobotSoftAssetInformation',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
