    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/uploadExcelRobotSoftAssetInformation',
        {
          ...this.searchValidateForm
        },
        {
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.download = '机器人资产信息'
      a.click()
      this.downloadLoading = false
    },

    // 导入Excel表格
    async importE(file) {
      this.importLoading = true
      var formData = new FormData()
      formData.append('file', file)

      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/importExcel',
        formData
      )
      this.importLoading = false
      if (res.state == '200') {
        this.$message({
          message: '导入成功！',
          type: 'success'
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        })
      }
    },

