      // // 隐藏echart的loadnig效果
      // this.myChart.hideLoading()

      this.option = {
        title: {
          text: '设备状态统计',
          subtext: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left'
        },
        series: [
          {
            name: '设备状态统计',
            type: 'pie',
            radius: '50%',
            data: this.statisticsStateList,
            label: {
              formatter: '{b}, {d}%'
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }

      this.myChart.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/scenicList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.scenicSpotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.searchValidateForm.scenicSpotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var scenicSpotId =
        searchScenicOptions[0].SCENIC_SPOT_ID /* 获取第一个景区id */
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.scenicSpotId = scenicSpotId
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      // this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.ascriptionCompanyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.ascriptionCompanyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.ascriptionCompanyId =
          searchCompanyOptions[0].COMPANY_ID
      }
    },

