    // 点击常规故障记录时触发
    normalFaultRecordRow(index, row) {
      // window.sessionStorage.setItem('rowRobotCode', row.robotCode)

      this.repairRecordDialogVisible = true
      this.repairRecordLoading = true
      this.repairRecordUrl = `/page/assetsSystem/robotServiceRecords/html/robotServiceRecordsList.html?robotCode=${row.robotCode}`
    },

    // 点击大维修记录时触发
    repairRecordRow(index, row) {
      // window.sessionStorage.setItem('rowRobotCode', row.robotCode)

      this.bigRepairRecordDialogVisible = true
      this.bigRepairRecordLoading = true
      this.bigRepairRecordUrl = `/page/assetsSystem/substituteApplySystem/index.html?robotCode=${row.robotCode}`
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row)
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象（防止改变表单数据时响应表格该行数据）

      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑机器人资产信息'
    },

    // 点击删除时触发
    delRow(index, row) {
      var softAssetInformationId = row.softAssetInformationId
      this.$confirm('确认删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.delTableData(softAssetInformationId)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == 'add') {
            // 新增公司信息
            console.log('新增公司信息')
            this.addTableData()
          } else if (this.dialogState == 'edit') {
            // 编辑公司信息
            console.log('编辑公司信息')
            this.editTableData()
          }

          this.$refs.validateForm.resetFields() // 重置表单（清空无效）
          this.getValidateForm() // 将validateForm对象中各个属性清空

          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 搜索表单设备状态改变触发
    onChangeSearchEquipmentStatus(type) {},

    // 提交表单设备状态改变触发
    onChangeEquipmentStatus(type) {
      console.log(type)
    },

    // 提交表单机器人编号改变触发
    onChangeRobotId(robotCode) {
      // console.log(robotCode)
      this.robotOptions.forEach((item) => {
        if (item.robotCode == robotCode) {
          this.validateForm.robotId = item.robotId
        }
      })
    },

    // 提交表单公司改变触发
    onChangeCompany(companyId) {
      // console.log(companyName)
      this.companyOptions.forEach((item) => {
        if (item.COMPANY_ID == companyId) {
          this.validateForm.ascriptionCompanyName = item.COMPANY_NAME
        }
      })
    },

    // 点击关闭设备统计dialog时触发
    handleCloseStatistic() {
      this.statisticDialogVisible = false
    },

    // 点击关闭调运轨迹dialog时触发
    handleCloseDispatchTrack() {
      this.dispatchTrackDialogVisible = false
    },

    // 调运轨迹页数变化时触发
    onDispatchTrackCurrentChange(currentPage) {
      this.dispatchTrailCurrentPage = currentPage
      this.getSysDepositLogList()
    },

    // 调运轨迹每页条数变化时触发
    onDispatchTrackSizeChange(pageSize) {
      this.dispatchTrailPageSize = pageSize
      this.getSysDepositLogList()
    },

    // 点击关闭故障记录dialog时触发
    handleCloseFailureRecord() {
      this.failureRecordDialogVisible = false
      this.bigRepairRecordLoading = false
    },

    // 点击关闭常见维修记录dialog时触发
    handleCloseRepairRecord() {
      this.repairRecordDialogVisible = false
      this.repairRecordLoading = false
    },

    // 点击关闭大维修记录dialog时触发
    handleCloseBigRepairRecord() {
      this.bigRepairRecordDialogVisible = false
    },

    // 故障记录页面加载完毕后触发
    onLoadFailureRepairRecord() {
      this.failureRecordLoading = false
    },

    // 常规维修记录页面加载完毕后触发
    onLoadRepairRecord() {
      this.repairRecordLoading = false
    },

    // 大维修记录页面加载完毕后触发
    onLoadBigRepairRecord() {
      this.bigRepairRecordLoading = false
    },

    // 导入EXCEL文件超出限制时触发
    handleImportExceed(files) {
      this.$refs.uploadImport.clearFiles()
      // const file = files[0]
      this.$refs.uploadImport.submit(files)
    },

    // 导入EXCEL文件状态改变时触发
    handleImportChange(uploadFile, uploadFiles) {
      console.log(uploadFile)
      this.importE(uploadFile.raw)
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
    DispatchTrail
  },
  computed: {
    activeFixed() {
      return (value) => {
        // 冻结左侧“合作主体、景区名称、机器人编号”
        if (
          value == '合作主体' ||
          value == 'scenicSpotName' ||
          value == 'robotCode'
        ) {
          return 'left'
        }
      }
    },

    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'netPrice') {
          result = row[value] || 0
        } else if (value == 'robotFaultState') {
          if (row[value] == '10') {
            result = '健康'
          } else {
            result = '不健康'
          }
        } else if (value == 'equipmentStatus') {
          if (row[value] == '10') {
            result = '在产'
          } else if (row[value] == '20') {
            result = '库存'
          } else if (row[value] == '30') {
            result = '运营'
          } else if (row[value] == '40') {
            result = '报废'
          } else {
            result = '无'
          }
        } else if (value == 'accumulate' || value == 'netPrice') {
          result = row[value] && row[value].toFixed(2)
        } else if (
          value == 'dayTime' ||
          value == 'monthTime' ||
          value == 'yearTime' ||
          value == 'runTime'
        ) {
          result = (!isNaN(row[value]) && (row[value] / 60).toFixed(2)) || 0
        } else if(value == 'errorRecordsAffect'){
			if(row.errorRecordsAffect == 0){
				result = '维修'
			}else if(row.errorRecordsAffect == 1){
				result = '故障'
			}else{
				result = '正常'
			}
		}
		else {
          result = row[value] || '无'
        }
        return result
      }
    }
  }
}
</script>

