<template>
  <div class="dispatch-trail" v-loading="trailLoading">
    <el-container>
      <el-main class="main-container">
        <div class="main">
          <el-steps class="steps" direction="vertical">
            <el-step
              class="step"
              v-for="(item, index) in list"
              :key="index"
              status="process"
            >
              <!-- 标题 start -->
              <template v-slot:title>
                <div>
                  <span>{{ item.createDate }}</span>
                  <!-- <span> [{{ item.robotDispatchUsersName }}] </span> -->
                  <el-tag class="step-title-tag" size="small ">{{
                    item.robotDispatchUsersName
                  }}</el-tag>
                  <span>发起申请</span>
                  <span
                    class="trial-detail-btn"
                    @click="detailRow(item.robotDispatchId)"
                    >查看调运详情</span
                  >
                </div>
              </template>
              <!-- 标题 start -->

              <!-- 内容 start -->
              <template v-slot:description>
                <div class="step-description">
                  <div class="mb-10 mt-5">
                    <span>从</span>
                    <b>
                      <el-tag size="mini">{{
                        item.robotDispatchCallOutName
                      }}</el-tag>
                    </b>
                    <span>调运至</span>
                    <b>
                      <el-tag size="mini">{{
                        item.robotDispatchTransferInName
                      }}</el-tag>
                    </b>
                  </div>
                  <div>
                    <el-timeline>
                      <el-timeline-item>
                        <span>发车日期: {{ '待开发' }}</span>
                      </el-timeline-item>
                      <el-timeline-item>
                        <span>到达日期: {{ '待开发' }}</span>
                      </el-timeline-item>
                      <el-timeline-item>
                        <span>完成调运: {{ '待开发' }}</span>
                      </el-timeline-item>
                    </el-timeline>
                  </div>
                </div>
              </template>
              <!-- 内容 end -->
            </el-step>
          </el-steps>
        </div>
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- 详情 dialog start -->
      <el-dialog
        class="dialog"
        title=""
        :visible.sync="detailDialogVisible"
        :before-close="detailHandleClose"
        :close-on-click-modal="false"
        append-to-body
      >
        <el-descriptions title="工厂发货详情信息" border>
          <el-descriptions-item
            v-for="(item, index) in detailTableList"
            :key="index"
            :label="index"
          >
            <template v-if="index == '发货图片' || index == '收货图片'">
              <el-image
                style="width: 100px;"
                :src="item"
                :preview-src-list="[item]"
              >
              </el-image>
            </template>
            <template v-else>
              {{ item }}
            </template>
          </el-descriptions-item>
        </el-descriptions>
      </el-dialog>
      <!-- 详情 dialog end -->
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'DispatchTrail',
  props: {
    total: {
      default: 0
    },
    'page-size': {
      default: 10
    },
    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      stepData: {}, // 步骤对象列表
      detailDialogVisible: false, // 是否显示详情dialog对话框
      trailLoading: false, // 轨迹loading状态
      detailTableList: [], // 详情列表

      currentPage: 1 // 当前页数
    }
  },
  created() {
    // this.login()
    // this.getTrailList()
  },
  mounted() {},
  updated() {
    // animejs动效（注意：必须要写在updated钩子函数中，因为我要监听的不是静态节点，而是动态节点，在mounted中是可以拿到静态节点后，但是由数据赋值的动态节点却拿不到）
    this.$anime({
      targets: '.steps .step',
      translateX: -150,
      delay: this.$anime.stagger(100) // 每个元素的延迟增加100毫秒。
    })
  },
  methods: {
    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 获取调运轨迹列表信息
     */
    async getTrailList() {
      // 从url上获取robotCode
      var search = window.location.search
      var robotCode = search.split('=')[1]

      this.trailLoading = true
      const { data: res } = await this.$http.get(
        '/system/factory_send/trailList',
        {
          params: {
            robotCode: robotCode
          }
        }
      )

      this.stepData = res.data
      this.trailLoading = false
    },

    /**
     * 调运轨迹详情信息
     */
    async detailTableDate(id) {
      const { data: res } = await this.$http.get(
        '/system/factory_send/detail',
        {
          params: {
            // form: 1, // 1、工厂发货；2、景区调度
            id: id // 当前编辑行id
          }
        }
      )
      var result = {}
      /**
       * 使用函数闭包特性，只需传入1次key值即可在内层函数中使用
       * 在这个函数中内层匿名函数与key变量共同组成了一个整体，构成闭包
       */
      function setResultProp(key) {
        return function(value) {
          if (value == '装车方式') {
            if (res.data[key] == 1) {
              result[value] = '人工装车'
            } else if (res.data[key] == 2) {
              result[value] = '叉车装车'
            } else {
              result[value] = ''
            }
          } else if (value == '发货进度') {
            if (res.data[key] == 1) {
              result[value] = '待发货'
            } else if (res.data[key] == 2) {
              result[value] = '已发货'
            } else if (res.data[key] == 3) {
              result[value] = '已收货'
            } else if (res.data[key] == 4) {
              result[value] = '已关闭'
            } else {
              result[value] = ''
            }
          } else {
            result[value] = res.data[key]
          }
        }
      }

      for (let key in res.data) {
        let f = setResultProp(key)
        switch (key) {
          case 'applicantName':
            f('申请人')
            break
          case 'applicantTime':
            f('申请时间')
            break
          case 'carType':
            f('货车类型')
            break
          case 'comsignmentNotes':
            f('发货备注')
            break
          case 'consigneeName':
            f('收货人')
            break
          case 'consigneePhone':
            f('收货人电话')
            break
          case 'consignmentCount':
            f('发货件数')
            break
          case 'consignmentDate':
            f('发货时间')
            break
          case 'consignmentPicture':
            f('发货图片')
            break
          case 'consignorName':
            f('发货人')
            break
          case 'driverName':
            f('驾驶员姓名')
            break
          case 'driverPhone':
            f('驾驶员电话')
            break
          case 'factoryName':
            f('发货工厂')
            break
          case 'license':
            f('车牌号')
            break
          case 'loadingCost':
            f('运费')
            break
          case 'loadingType':
            f('装车方式')
            break
          case 'receiningAddress':
            f('收货地址')
            break
          case 'receivingCount':
            f('收货数量')
            break
          case 'receivingDate':
            f('收货时间')
            break
          case 'receivingName':
            f('收货景区')
            break
          case 'receivingNotes':
            f('收货备注')
            break
          case 'receivingPicture':
            f('收货图片')
            break
          case 'receivingRobotCount':
            f('收货机器人数量')
            break
          case 'robotCodes':
            f('设备编码')
            break
          case 'robotCount':
            f('机器人数量')
            break
          case 'type':
            f('发货进度')
            break
          default:
            break
        }
      }

      this.detailTableList = result
    },

    // 点击详情时触发
    detailRow(id) {
      this.detailDialogVisible = true
      this.dialogState = 'detail'
      this.detailTableDate(id)
    },

    // 详情 dialog 取消
    detailDialogClose() {
      this.detailDialogVisible = false
    },
    // 详情 dialog 右上角关闭按钮
    detailHandleClose(done) {
      done()
    },

    // currentPage 变化时触发
    onCurrentChange(currentPage) {
      this.$emit('current-change', currentPage)
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      this.$emit('size-change', pageSize)
    }
  },
  components: {}
}
</script>

<style lang="scss" scoped>
.dispatch-trail {
  .main-container {
    position: relative;
    left: -50px;
  }
  .main {
    overflow: hidden;
    padding-left: 60px;
  }
}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f2f2f2;
  margin-bottom: 10px;
  height: 80px !important;
}
.header > * {
  /**设置header下的所有子元素样式 */
  // margin: 0 10px;
  display: flex;
}
.header .input-name {
  width: 150px;
}

.header .el-form-item {
  margin-bottom: 0;
}

.el-header,
.el-main {
  padding: 0;
}

.upload-demo {
  margin-left: 10px;
}

.steps {
  transform: translateX(150px);
  /* overflow: hidden; */
}

.step-title-tag {
  font-size: 16px;
}

.trial-detail-btn {
  background: #009688;
  border-radius: 3px;
  color: #ffffff;
  font-size: 14px;
  padding: 2px 5px;
  margin-left: 10px;
  cursor: pointer;
  user-select: none;
  transition: all 0.5s;
}
.trial-detail-btn:hover {
  background: #00880a;
}

.mb-10 {
  margin-bottom: 10px;
}
.mt-5 {
  margin-top: 5px;
}

.step-description {
  // background: #d9e5f9;
  padding: 10px;
}
</style>
